.radioButtonsContainer {
  display: flex;
  align-items: center;
  gap: 24px;
  margin-top: 25px;
}

.radioButton {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.radioButtonInput {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.radioButtonLabel {
  display: inline-block;
  padding-left: 30px;
  margin-bottom: 10px;
  position: relative;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}

.radioButtonCustom {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid #555;
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}

.radioButtonInput:checked + .radioButtonLabel .radioButtonCustom {
  transform: translateY(-50%) scale(0.9);
  border: 5px solid #4c8bf5;
  color: #4c8bf5;
}

.radioButtonInput:checked + .radioButtonLabel {
  color: #4c8bf5;
}

.radioButtonLabel:hover .radioButtonCustom {
  transform: translateY(-50%) scale(1.2);
  border-color: #4c8bf5;
  box-shadow: 0 0 10px #4c8bf580;
}
